.BuildControl {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px 0;
}

.BuildControl button {
    display: block;
    font: inherit;
    padding: 5px;
    margin: 0 5px;
    width: 80px;
    border: 1px solid #AA6817;
    cursor: pointer;
    outline: none;
}

.BuildControl button:disabled {
    background-color: #AC9980;
    border: 1px solid #7E7365;
    color: #ccc;
    cursor: default;
}

.BuildControl button:hover:disabled {
    background-color: #AC9980;
    color: #ccc;
    cursor: not-allowed;
}

.Label {
    padding: 10px;
    font-weight: bold;
    width: 80px;
}

.Price {
    padding: 10px;
    font-weight: bold;
    width: 30px;
    font-size: 20px
}

.BuildControl .Less {  
    width:50px;
    height:50px;
    border-radius: 50%;
    background-color: #D39952;
    color: white;
    font-size: 30px;
}

.BuildControl .More {
    width:50px;
    height:50px;
    border-radius: 50%;
    background-color: #8F5E1E;
    color: white;
}

.BuildControl .Less:hover, .BuildControl .Less:active {  
    background-color: #DAA972;
    color: white;
}

.BuildControl .More:hover,.BuildControl .More:active {
    background-color: #99703F;
    color: white;
}

.More:hover {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(10px) translateZ(1px);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0);
}

.Less:hover {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(10px) translateZ(1px);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0);
}

@media (min-width: 500px){
    .Label {
        font-size: 20px;
        margin-right: 15px
    }
}
